import type Cryptr from "cryptr";
import { GoogleSpreadsheet, GoogleSpreadsheetRow } from "google-spreadsheet";

export const secrets =
  "";

let DOC: GoogleSpreadsheet | null = null;

export const demoSheet = async () => {
  const pass = window.localStorage.getItem("pass");
  if (pass == null) {
    throw new Error("No saved password.");
  }
  const cryptr: Cryptr = new (
    window as unknown as { Cryptr: typeof Cryptr }
  ).Cryptr(pass);
  let details: {
    private_key: string;
    sheet: string;
    client_email: string;
  } | null = null;
  try {
    const stringResult = cryptr.decrypt(secrets);
    details = JSON.parse(stringResult);
  } catch (e) {
    throw new Error("FAILED: to login with password");
  }

  if (details == null) {
    throw new Error("FAILED: issue parsing");
  }
  // Initialize the sheet - doc ID is the long id in the sheets URL
  const doc: GoogleSpreadsheet = new (window as any).gsheet.GoogleSpreadsheet(
    details.sheet
  );

  // Initialize Auth - see https://theoephraim.github.io/node-google-spreadsheet/#/getting-started/authentication
  await doc.useServiceAccountAuth({
    // env var values are copied from service account credentials generated by google
    // see "Authentication" section in docs for more info
    client_email: details.client_email,
    private_key: details.private_key,
  });

  await doc.loadInfo(); // loads document properties and worksheets
  DOC = doc;
};

export const getSteps = async () => {
  if (DOC === null) {
    await demoSheet();
  }
  const team = window.localStorage.getItem("team");
  if (team === null) {
    return [] as GoogleSpreadsheetRow[];
  }

  const sheet = DOC!.sheetsByTitle[team]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]
  const rows = await sheet.getRows(); // can pass in { limit, offset }
  return rows;
  // console.log("LOL", rows[0].HEY);
  // rows[1].HEY = "pizza";
  // await rows[1].save();

  // adding / removing sheets
  // const newSheet = await doc.addSheet({ title: "Sheet1" });
  // await newSheet.delete();
};

export const getStep = async (
  stepKey: string
): Promise<GoogleSpreadsheetRow> => {
  if (DOC === null) {
    await demoSheet();
  }

  const sheet = DOC!.sheetsByIndex[0]; // or use doc.sheetsById[id] or doc.sheetsByTitle[title]
  const rows = await sheet.getRows(); // can pass in { limit, offset }
  let findRow: GoogleSpreadsheetRow | null = null;
  for (const row of rows) {
    if (row.step_id === stepKey) {
      findRow = row;
      break;
    }
  }
  if (findRow == null) {
    throw new Error(`Row not found for key: ${stepKey}`);
  }

  return findRow!;
};

export const loadTeams = async (): Promise<string[]> => {
  if (DOC === null) {
    await demoSheet();
  }

  const sheets = DOC!.sheetsByTitle;
  const teams = Object.keys(sheets).filter((title) => title.startsWith("Team"));
  return teams.sort();
};

export const loadRules = async (): Promise<string[]> => {
  if (DOC === null) {
    await demoSheet();
  }

  const rules = DOC!.sheetsByTitle["Rules"];
  const rows = await rules.getRows();
  return rows.map((r) => r.rules as string);
};
