import { memo, useMemo, useState } from "react";
import steps from "./steps.json";
import * as emoji from "neato-emoji-converter";
import * as Ons from "react-onsenui";
import { GoogleSpreadsheetRow } from "google-spreadsheet";
import { getSteps } from "../../sheets";

const conv = new emoji();
console.log(conv);

const ListingImpl = ({
  navigator,
  title,
}: {
  navigator: Ons.Navigator;
  title: string;
}) => {
  const [rows, setRows] = useState<GoogleSpreadsheetRow[] | null>(null);

  useMemo(async () => {
    const stepRows = await getSteps();
    setRows(stepRows);
  }, []);
  return (
    <Ons.Page>
      <Ons.Toolbar inline>
        <div className="left">
          <div className="left">
            <Ons.BackButton>Back</Ons.BackButton>
          </div>
        </div>
        <div className="center">{title}</div>
        <div className="right"></div>
      </Ons.Toolbar>
      {rows != null ? (
        <Ons.List
          dataSource={rows}
          renderRow={(
            row: {
              step_emoji: string;
              step_name: string;
              step_id: string;
              step_description: string;
            },
            idx: number
          ) => (
            <Ons.ListItem
              tappable
              onClick={() =>
                navigator.pushPage({
                  key: "STEP",
                  stepKey: row.step_id,
                  title: row.step_name,
                })
              }
              modifier={
                idx === (rows?.length ?? 0) - 1
                  ? "longdivider chevron"
                  : "chevron"
              }
            >
              <span style={{ marginRight: 10 }}>{row.step_emoji}</span>
              {row.step_name}
            </Ons.ListItem>
          )}
        />
      ) : (
        <div
          style={{
            width: "100%",
            marginTop: 25,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <Ons.ProgressCircular indeterminate />
        </div>
      )}
    </Ons.Page>
  );
};

export const Listing = memo(ListingImpl);
