import React from "react";
import logo from "./logo.svg";
import { Counter } from "./features/counter/Counter";
import "./App.css";
import "onsenui/css/onsenui.css";
import "./onsen-css-components.css";
import * as Ons from "react-onsenui";
import { Listing } from "./pages/listing";
import { Login } from "./pages/login";
import { Step } from "./pages/step";
import { Rules } from "./pages/rules";

type RouteKey =
  | {
      key: "LOGIN";
      title: string;
    }
  | {
      key: "RULES";
      title: string;
    }
  | {
      key: "LISTING";
      title: string;
    }
  | {
      key: "STEP";
      stepKey: string; // TODO: should be using data property of navigator pushPage
      title: string;
    };

const initialRouteStack = () => {
  const pass = localStorage.getItem("pass");
  if (pass != null) {
    const team = localStorage.getItem("team");
    if (team != null) {
      return [
        {
          key: "RULES",
          title: "Rules & Team",
        },
        {
          key: "LISTING",
          title: "Steps",
        },
      ];
    }
    return [
      {
        key: "RULES",
        title: "Rules & Team",
      },
    ];
  }
  return [
    {
      key: "LOGIN",
      title: "Login",
    },
  ];
};
function App() {
  return (
    <Ons.Navigator
      initialRouteStack={initialRouteStack()}
      renderPage={(route: RouteKey, navigator: Ons.Navigator) => {
        switch (route.key) {
          case "LISTING":
            return <Listing navigator={navigator} title={route.title} />;
          case "LOGIN":
            return <Login navigator={navigator} title={route.title} />;
          case "RULES":
            return <Rules navigator={navigator} title={route.title} />;
          case "STEP": {
            return (
              <Step
                navigator={navigator}
                title={route.title}
                stepKey={route.stepKey}
              />
            );
          }
          default:
            return <pre>{JSON.stringify(route)}</pre>;
        }
      }}
    />
  );
}

export default App;
